html{box-sizing:border-box}*,:after,:before{box-sizing:inherit}a,abbr,acronym,address,applet,article,aside,audio,b,big,blockquote,body,canvas,caption,center,cite,code,dd,del,details,dfn,div,dl,dt,em,embed,fieldset,figcaption,figure,footer,form,h1,h2,h3,h4,h5,h6,header,hgroup,html,i,iframe,img,ins,kbd,label,legend,li,mark,menu,nav,object,ol,output,p,pre,q,ruby,s,samp,section,small,span,strike,strong,sub,summary,sup,table,tbody,td,tfoot,th,thead,time,tr,tt,u,ul,var,video{margin:0;padding:0;border:0;font-size:100%;font:inherit;vertical-align:baseline;line-height:1.5em}h1,h2,h3,h4,h5,h6{font-weight:700}h1{font-size:3em}h2{font-size:2.25em}h3{font-size:1.75em}h4{font-size:1.25em}h5{font-size:1.075em}h6{font-size:1em}strong{font-weight:700}em{font-style:italic}article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section{display:block}ol,ul{list-style:none}blockquote,q{quotes:none}blockquote:after,blockquote:before,q:after,q:before{content:'';content:none}table{border-collapse:collapse;border-spacing:0}button,input[type=submit]{background:0 0;box-shadow:none;border:none;cursor:pointer}button:focus,input:focus{outline:0}input[type=text]{background:0 0;outline:0;box-shadow:none;border:none}textarea{border:none}